import uniPopup from "@/components/uni-popup/uni-popup.vue"
export default {
	components: {
		uniPopup
	},
	data() {
		return {
			// 线路特色富文本的内容
			textVal: '<h1>这是富文本内容</h1>',
			// 暂时的临时数据 后期去掉
			id: '',
		}
	},
	methods: {
		// 判断是否登录了 登录了下单 未去登录页面
		open() {
			// uni.login({
			// 	provider: 'weixin',
			// 	success: (res) => {
			// 		console.log(res)
			// 		this.getUser()
			// 	}
			// });
			// 去选择出行时间 / 人数页面
			uni.navigateTo({
				url: '/pages/choice/choice'
			});
		},
		// 获取用户信息
		getUser() {
			uni.getUserInfo({
				provider: 'weixin',
				success: (res) => {
					console.log(res)
					if (res.errMsg == 'getUserInfo:ok') {
						this.$refs.popup.open()
					}
				},
				fail: err => {
					console.log(err)
				}
			});
		},
		// 去登录页面
		goLogin() {
			uni.navigateTo({
				url: '/pages/login/login'
			});
		},
		// 拨打号码
		goMakePhoneCall() {
			uni.makePhoneCall({
				phoneNumber: '17855355076' //仅为示例
			});
		}
	},
	onLoad() {}
}
